/** @format */

import React from 'react'

class Experience extends React.Component {
	constructor(props) {
		super()
    }

	render() {
        let description = this.props.description.map( element => {
            return <li className="experience-list-element">{element}</li>
        })
		return (
            <div className="experience-container">
                <div className="experience-header"><a href={this.props.website} className="inline-link">{this.props.company}</a> - {this.props.location}
                    <div className="experience-description">{this.props.title}  {this.props.time}</div>
                </div>
                    {description}
			</div>
		)
	}
}
export default Experience
