/** @format */

import React from 'react'
import PopUp from './PopUp'

class Project extends React.Component {
	constructor(props) {
		super()
		this.state = {
			showPopup: false
		}
	}
	togglePopup() {
		this.setState({
			showPopup: !this.state.showPopup
		})
	}

	render() {
		return (
			<div>
				<div className="project-link" onClick={this.togglePopup.bind(this)}>
					<div className="flex-element project-wrapper">
						<div className="project-title"> {this.props.name} </div>{' '}
						<div className="project-description"> {this.props.description} </div>{' '}
					</div>{' '}
				</div>
				{this.state.showPopup ? (
					<PopUp
						description={this.props.descriptionPlus ? this.props.descriptionPlus : this.props.description}
						name={this.props.name}
						technologies={this.props.technologies}
						closePopup={this.togglePopup.bind(this)}
						url={this.props.url ? this.props.url : ''}
					/>
				) : null}{' '}
			</div>
		)
	} // https://www.youtube.com/watch?v=iE_6pQ3RlZU
}
export default Project
