/** @format */
import resume from './Resume.pdf'
import React from 'react'
class ProfileLink extends React.Component {
	constructor(props) {
		super()
	}
	render() {
		let jsx
		if (this.props.url) {
			jsx = (
				<a href={this.props.url}>
					<span>
						{' '}
						<img
							className="icon-image"
							src={require('./Icons/' + this.props.name + '.png')}
							alt="Can't Load"
						/>{' '}
					</span>{' '}
				</a>
			)
		} else {
			jsx = (
				<a href={resume} target="noopener noreferrer">
					<span>
						<img
							className="icon-image"
							src={require('./Icons/' + this.props.name + '.png')}
							alt="Can't Load"
						/>{' '}
					</span>{' '}
				</a>
			)
		}
		return <div className="flex-element"> {jsx} </div>
	}
}
export default ProfileLink
