import React from 'react'

class PopUp extends React.Component {
	constructor(props) {
		super()
	}
	render() {
		return (
			<div className="popup">
				<div className="popup_inner">
					<h1> {this.props.name} </h1>
					<p> {this.props.description} </p>
					<div className="flex-container box-test">
						{this.props.technologies.map((logo, index) => {
							return (
								<img
									className="flex-element flex-popup popup-images"
									src={require('./Icons/' + logo + '.png')}
									alt="Can't Load"
								/>
							)
						})}
						<div className="flex-break"></div>
						{this.props.url !== '' && (
							<a
								className="flex-popup-link"
								href={this.props.url}>
								Visit Website
							</a>
						)}
					</div>
					<button
						onClick={this.props.closePopup}
						className="close-btn">
						&times;
					</button>
				</div>
			</div>
		)
	}
}
export default PopUp
