import React from "react";

export default class MyForm extends React.Component {
  constructor(props) {
    super(props);
    this.submitForm = this.submitForm.bind(this);
    this.state = {
      status: ""
    };
  }

  render() {
    const { status } = this.state;
    return (
      
		<div className="contact-form-container">
			<form
        onSubmit={this.submitForm}
        action="https://formspree.io/f/xyybbkob"
        method="POST"
      >
			<div className="contact-form-label-wrapper">
				<label className="contact-form-label">
					Have a question or want to work together? Shoot me a
					message.
				</label>
			</div>

			<div id="contact-form-name" className="contact-form-element">
				<input
					className="input-contact-name"
					type="text" 
					name="name"
					placeholder="Name"></input>
			</div>
			<div id="contact-form-email" className="contact-form-element">
				<input
					className="input-contact-email"
					type="email" 
					name="_replyto"
					placeholder="Enter Email"></input>
			</div>
			<div id="contact-form-subject" className="contact-form-element">
				<input
					className="input-contact-subject"
					type="text"
					name="subject"
					placeholder="Subject"></input>
			</div>
			<div
				id="contact-form-email"
				className="contact-form-element-textarea">
				<textarea
					className="input-contact-message"
					type="text" 
					name="message"
					placeholder="Your Message"></textarea>
				<br></br>
				{status === "ERROR" ?  <p>Ooops! There was an error. Try again later</p> : status === "SUCCESS" ? <p>Message Sent! I will get back as soon as I can.</p> : <button className="contact-form-submit btn">Submit</button>}
			</div>


			
		</form>
	  </div>
    );
  }

  submitForm(ev) {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader("Accept", "application/json");
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        this.setState({ status: "SUCCESS" });
      } else {
        this.setState({ status: "ERROR" });
      }
    };
    xhr.send(data);
  }
}
